<template>
    <div class="service" v-loading="loading">
        <div class="service-content" v-if="orderList">
            <div class="box">
                <div class="info" v-for="(item,key) in orderList.getProductInfo" :key="key">
                    <img :src="item.smallImg" alt="" class="img">
                    <div class="text">
                        <p class="orderInfo">{{item.chrName}}</p>
                        <p class="gray">{{item.goodsSpec}}</p>
                        <p class="gray">×{{item.goodsNumber}}</p>
                    </div>
                </div>
            </div>
            <div class="box">
                <span class="black">{{ $L('可退金额')}}：</span><span class="red">{{ orderList.actualPrice }}</span>
            </div>
            <div class="box gray">
                {{$L('*该产品仅退回产品金额，已使用的积分,优惠券和快递费用将不予退还')}}
            </div>
            <div class="reason">
                <p class="title">{{$L('退货/退款原因')}}</p>
                <el-input maxlength="500" :rows="7" type="textarea" resize="none" v-model="remarks" :placeholder="$L('请输入原因')" class="remarks"></el-input>
                <div class="up-load-pic">
                    <ul class="img-list">
                        <li class="load-img" v-for="(item,index) in showImgArr" :key="index">
                            <div class="upload-list-cover">
                                <span class="bbx-font bbx-icon-shanchu" @click="handleRemove(index)"></span>
                            </div>
                            <img class="img" :src="item" />
                        </li>
                        <li class="load-img up-load-img" v-if="showImgArr.length < maxNum " >
                            <span class="bbx-font bbx-icon-jiahao"></span>
                            <input type="file" name="CommentsPic" class="upload" multiple accept="image/png,image/jpeg,image/jpg" @change="upImgFunc"/>
                        </li>
                    </ul>
<!--                    <span class="img-num">{{uploadLen}}/4</span>-->
                </div>
                <p class="size gray">*{{ $L('图片大于2M将无法上传') }}</p>
                <el-button type="primary" @click="submitFunc">{{ $L('申请退款') }}</el-button>
            </div>
        </div>
    </div>
</template>

<script>
    export default {
        name: "orderService",
        data() {
            return {
                loading:true,
                errorType: 'error',
                errorTitle: this.$L('出错了'),
                errorTips: null,
                errorShow: false,
                remarks: '',
                showImgArr:[],
                maxNum: 4,
                uploadList: [],
                uploadImgArr:[],
                orderList: {},
                imgList:[],
                uploadLen:0
            }
        },
        created () {
            this.getData();
        },
        methods:{
            showError(title, tips, type) {
                if (type === 'warning') {
                    this.errorType = 'warning';
                } else {
                    this.errorType = 'error';
                }
                if (title && title !== '') {
                    this.errorTitle = title;
                } else {
                    this.errorTitle = this.$L('出错了');
                }
                if (tips && tips !== '') {
                    this.errorTips = tips;
                } else {
                    this.errorTips = null;
                }
                this.loading = false;
                this.errorShow = true;
            },
            backFunc() {
                this.$router.back(-1);
            },
            getData() {
                const _self = this;
                _self.loading = true;
                let id = _self.$route.params.id;
                _self.$api('applyRefund', {orderId: id}, function (reData) {
                    if (reData.flag === 1) {
                        _self.orderList = reData.data;
                        _self.loading = false;
                    } else if (reData.flag === -1) {
                        //登录超时了
                        _self.$confirm(_self.$L('请重新登录'), _self.$L('登录超时'), {
                            confirmButtonText: _self.$L('重新登录'),
                            cancelButtonText: _self.$L('取消'),
                            type: 'error',
                            distinguishCancelAndClose: true
                        }).then(() => {
                            _self.$router.push({name: 'login', query: {redirect: _self.$route.fullPath}});
                        }).catch(action => {
                            _self.$gotoWebHome();
                        });
                    } else {
                        //未知错误
                        _self.$confirm(_self.$L('数据加载失败'), _self.$L('出错了'), {
                            confirmButtonText: _self.$L('点击重试'),
                            cancelButtonText: _self.$L('返回首页'),
                            type: 'error',
                            distinguishCancelAndClose: true
                        }).then(() => {
                            _self.getData();
                        }).catch(action => {
                            _self.$router.push({name:'orderList',params:{type:'all'}});
                        });
                    }
                }, function (error) {
                    //网络请求错误
                    _self.$confirm(_self.$L('网络请求失败'), _self.$L('出错了'), {
                        confirmButtonText: _self.$L('点击重试'),
                        cancelButtonText: _self.$L('返回首页'),
                        type: 'error',
                        distinguishCancelAndClose: true
                    }).then(() => {
                        _self.getData();
                    }).catch(action => {
                        _self.$router.push({name:'orderList',params:{type:'all'}});
                    });
                });
            },
            submitFunc(){
                const _self = this;
                _self.loading = true;
                if(_self.remarks !== ''){
                    let id = _self.orderList.orderId;
                    _self.imgList[id] = {
                        'fileList':_self.uploadList
                    };
                    let arr = {
                        orderId:id,
                        refundResult:_self.remarks,
                        'choose_img': {
                            fileList: _self.imgList
                        }
                    };

                    let postData = _self.$objToFormData(arr);
                    _self.$api('addApplyRefund', postData, function (reData) {
                        if (reData.flag === 1) {
                            _self.getData();
                            _self.$message({
                                type: 'success',
                                message: _self.$L('提交成功!')
                            });
                            _self.$router.push({name:'orderServiceInfo',parmars:{orderId:id}});
                            _self.loading = false;
                        } else if (reData.flag === -1) {
                            //登录超时了
                            _self.$confirm(_self.$L('请重新登录'), _self.$L('登录超时'), {
                                confirmButtonText: _self.$L('重新登录'),
                                cancelButtonText: _self.$L('取消'),
                                type: 'error',
                                distinguishCancelAndClose: true
                            }).then(() => {
                                _self.$router.push({name: 'login', query: {redirect: _self.$route.fullPath}});
                            }).catch(action => {
                                _self.$gotoWebHome();
                            });
                        } else {
                            //未知错误
                            _self.$confirm(_self.$L('数据加载失败'), _self.$L('出错了'), {
                                confirmButtonText: _self.$L('点击重试'),
                                cancelButtonText: _self.$L('返回首页'),
                                type: 'error',
                                distinguishCancelAndClose: true
                            }).then(() => {
                                _self.getData();
                            }).catch(action => {
                                _self.$router.push({name:'orderList',params:{type:'all'}});
                            });
                        }
                    }, function (error) {
                        //网络请求错误
                        _self.$confirm(_self.$L('网络请求失败'), _self.$L('出错了'), {
                            confirmButtonText: _self.$L('点击重试'),
                            cancelButtonText: _self.$L('返回首页'),
                            type: 'error',
                            distinguishCancelAndClose: true
                        }).then(() => {
                            _self.getData();
                        }).catch(action => {
                            _self.$router.push({name:'orderList',params:{type:'all'}});
                        });
                    }, true);
                } else {
                    _self.$message({
                        type: 'error',
                        message: _self.$L('请填写退款申请原因!')
                    });
                    _self.loading = false;
                }
            },
            //上传图片
            upImgFunc(event) {
                const _self = this;
                let files = event.target.files;
                let oldLen = _self.uploadLen;
                let len = files.length+oldLen;
                if(len < _self.maxNum){
                    _self.$message({
                        showClose: true,
                        message: '最多可上传'+_self.maxNum+'张，您还可以上传'+(_self.maxNum - len)+'张',
                        type: 'success'
                    });
                }
                if(files.length <= _self.maxNum){
                    for (let i=0; i < files.length; i++) {
                        let imgUrl = null;
                        let size = Math.floor(files[i].size / 1024);
                        let type = files[i].type.substring(6);
                        if (size > 2*1024*1024) {
                            _self.$Message({
                                showClose: true,
                                message: '请选择2M以内的图片！',
                                type: 'warning'
                            });
                            return false;
                        }
                        if(!(type === 'png' || type === 'jpeg' || type === 'jpg')){
                            _self.$Message({
                                showClose: true,
                                message: '请选择png、jpeg、jpg的图片格式！',
                                type: 'warning'
                            });
                            return false;
                        }
                        _self.uploadList.push({
                            'file':files[i]
                        });
                        _self.uploadLen++;
                        let reader = new FileReader();
                        reader.readAsDataURL(files[i]);
                        if(Number(_self.showImgArr.length + files.length) <= _self.maxNum){
                            reader.onload = function(evt){
                                imgUrl = evt.target.result;
                                _self.showImgArr.push(imgUrl);
                            }
                        } else {
                            if(  _self.showImgArr.length <= _self.maxNum ){
                                _self.$message({
                                    showClose: true,
                                    message: '最多可上传'+_self.maxNum+'张',
                                    type: 'success'
                                });
                            }
                        }

                    }
                } else {
                    _self.$message({
                        showClose: true,
                        message: '最多可上传'+_self.maxNum+'张!',
                        type: 'success'
                    });
                }

            },
            //删除图片
            handleRemove(index){
                this.uploadList.splice(index, 1);
                this.showImgArr.splice(index, 1);
                this.uploadLen = this.showImgArr.length;
            },
        }
    }
</script>

<style lang="scss" scoped>
    @import "../../../assets/css/common/lib.scss";
    @import "../../../assets/icon/iconfont.css";
    .service{
        padding-top: 10px;
        background-color: #fff;
        .service-content {
            width: 100%;
            .box{
                border-bottom: 1px solid #E1E1E1;
                padding: 10px;
                font-size: 12px;
            }
            .info{
                .img{
                    width: 80px;
                    height: 80px;
                }
                .text{
                    display: inline-block;
                    vertical-align: top;
                    margin-left: 10px;
                }
            }
            .reason{
                padding: 10px;
                .title{
                    font-size: 14px;
                    margin-bottom: 10px;
                }
                .up-load-pic{
                    padding: 20px 0 10px;
                    .img-list{
                        overflow: hidden;
                        display: inline-block;
                        .load-img{
                            width: 65px;
                            height: 65px;
                            line-height: 65px;
                            float: left;
                            text-align: center;
                            position: relative;
                            overflow: hidden;
                            margin-right: 10px;
                            &:last-child{
                                margin-right: 0;
                            }
                            &.up-load-img{
                                background-color: #F0F1F5;
                                text-align: center;
                                margin-right: 15px;
                            }
                            &:hover .upload-list-cover{
                                display: block;
                            }
                            .img{
                                width: 100%;
                                height: auto;
                            }
                            .upload-list-cover{
                                display: none;
                                position: absolute;
                                top: 0;
                                bottom: 0;
                                left: 0;
                                right: 0;
                                background: rgba(0,0,0,.6);
                                .bbx-font{
                                    color: #fff;
                                    font-size: 16px;
                                    cursor: pointer;
                                }
                            }
                            .upload{
                                position: absolute;
                                top: 0;
                                left: 0;
                                width: 100%;
                                height: 100%;
                                opacity: 0;
                                cursor: pointer;
                            }
                        }
                    }
                    .img-num{
                        line-height: 1;
                    }
                }
                .size{
                    font-size: 12px;
                    margin-bottom: 20px;
                }
            }
            .el-button{
                width: 100%;
                height: 50px;
                font-size: 14px;
            }
        }

        .red{
            color: #FF0000;
        }
        .black{
            color: #333;
            font-weight: 700;
        }
        .gray{
            color: #999999;
        }
    }
</style>
<style scoped>
    .el-message-box {
        width: 94% !important;
    }
</style>